.field > button:first-child, .field > span:first-child {
    margin-left: 0
}

.field > button:last-child, .field > span:last-child {
    margin-right: 0
}

button[type=reset] {
    background-image: url('data:image/svg+xml;utf8,<svg width="14" height="14" viewBox="0 0 14 14" xmlns="http://www.w3.org/2000/svg"><path fill-rule="evenodd" fill="#999" d="M6.327 7L0 .673.673 0 7 6.327 13.327 0 14 .673 7.673 7 14 13.327l-.673.673L7 7.673.673 14 0 13.327 6.327 7z"/></svg>')
}

button[type=reset]:focus, button[type=reset]:hover {
    background-image: url('data:image/svg+xml;utf8,<svg width="14" height="14" viewBox="0 0 14 14" xmlns="http://www.w3.org/2000/svg"><path fill-rule="evenodd" d="M6.327 7L0 .673.673 0 7 6.327 13.327 0 14 .673 7.673 7 14 13.327l-.673.673L7 7.673.673 14 0 13.327 6.327 7z"/></svg>')
}

button[type=subbmit] {
    background-image: url('data:image/svg+xml;utf8,<svg width="28" height="16" viewBox="0 0 28 16" xmlns="http://www.w3.org/2000/svg"><path fill-rule="evenodd" fill="#999" d="M26.275 7.52L20.145.632 20.882 0l6.838 7.684L28 8l-.281.316L20.88 16l-.735-.633 6.129-6.887H0v-.96h26.275z"/></svg>')
}

button[type=subbmit]:focus, button[type=subbmit]:hover {
    background-image: url('data:image/svg+xml;utf8,<svg width="28" height="16" viewBox="0 0 28 16" xmlns="http://www.w3.org/2000/svg"><path fill-rule="evenodd" d="M26.275 7.52L20.145.632 20.882 0l6.838 7.684L28 8l-.281.316L20.88 16l-.735-.633 6.129-6.887H0v-.96h26.275z"/></svg>')
}

.field > .search {
    background-image: url('data:image/svg+xml;utf8,<svg width="14" height="14" viewBox="0 0 14 14" xmlns="http://www.w3.org/2000/svg"><path fill-rule="evenodd" d="M9.353 10.043a5.69 5.69 0 1 1 .69-.69L14 13.31l-.69.69-3.957-3.957zm1.05-4.353a4.714 4.714 0 1 1-9.428 0 4.714 4.714 0 0 1 9.429 0z"/></svg>')
}

textarea {
    display: block;
    width: 100%;
    min-height: 115px;
    padding: 15px 15px 15px 10px;
    border: 1px solid #ccc;
    font-size: 18px;
    line-height: 25px;
    resize: vertical;
    overflow: auto
}

textarea.ghost {
    padding: 0;
    border: none;
    border-bottom: 1px solid #ccc
}

.control {
    display: inline-block;
    vertical-align: middle;
    font-size: 0;
    text-transform: capitalize;
}

.control * {
    cursor: pointer
}

input[type=checkbox], input[type=radio] {
    display: inline-block;
    vertical-align: top;
    width: 25px;
    height: 25px;
    will-change: border-color;
    -webkit-transition: border-color .16s ease;
    -o-transition: border-color .16s ease;
    transition: border-color .16s ease
}

input[type=radio] {
    border-radius: 25px
}

input[type=checkbox]:focus, input[type=checkbox]:hover, input[type=radio]:focus,
input[type=radio]:hover {
    border: 1px solid #999
}

input[type=checkbox]:checked, input[type=radio]:checked {
    background-image: url('data:image/svg+xml;utf8,<svg width="16" height="14" viewBox="0 0 16 14" xmlns="http://www.w3.org/2000/svg"><path fill-rule="evenodd" d="M16 1.131L7.47 13.229 6.926 14l-.765-.54L0 9.103l1.079-1.592 5.396 3.815L14.462 0 16 1.131z"/></svg>');
    background-repeat: no-repeat;
    background-position: 50%
}

input[type=radio]:checked {
    background-image: url('data:image/svg+xml;utf8,<svg width="13" height="13" viewBox="0 0 13 13" xmlns="http://www.w3.org/2000/svg"><path d="M0 6.5a6.5 6.5 0 1 1 13 0 6.5 6.5 0 0 1-13 0z"/></svg>')
}

.control > span {
    display: inline-block;
    vertical-align: top;
    margin-left: 10px;
    font-size: 18px;
    line-height: 25px
}

.control-group {
    display: -ms-flexbox;
    display: flex;
    border: 1px solid #ccc;
    border-right: none;
    font-size: 0
}

.control-group .control {
    position: relative;
    width: 100%;
    height: 45px;
    border-right: 1px solid #ccc;
    text-align: center;
    white-space: nowrap;
    overflow: hidden
}

.control-group input[type=checkbox], .control-group input[type=radio] {
    position: absolute;
    z-index: 1;
    width: 100%;
    height: 100%;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    border: none;
    border-radius: 0;
    -webkit-transition: background-color .16s ease;
    -o-transition: background-color .16s ease;
    transition: background-color .16s ease
}

.control-group span {
    position: relative;
    z-index: 2;
    margin: 0;
    line-height: 45px;
    will-change: color;
    -webkit-transition: color .16s ease;
    -o-transition: color .16s ease;
    transition: color .16s ease
}

.control-group .control:hover input[type=checkbox] + span, .control-group .control:hover input[type=radio] + span,
.control-group .control input[type=checkbox]:focus + span, .control-group .control input[type=radio]:focus + span {
    color: #999
}

.control-group input[type=checkbox]:checked, .control-group input[type=radio]:checked {
    background-image: none;
    background-color: #f5f5f5
}


